/*=============================================
      ОБЩИЕ ПРАВИЛА
  ============================================= */

.clearfix:after::after {
  display: block;
  content: "";
  clear: both;
}

.wrapper {
  max-width: 1000px;
  margin: 0 auto;
  padding-left: 100px;
  padding-right: 100px;
  box-sizing: content-box;
}

html {
  font-size: 100%;
}

body {
  font-family: "ArialMT", sans-serif;
  color: #000000;
}

.hide {
  display: none;
}

/*============================================= */

/*=============================================
        ШАПКА
============================================= */

.header {
  background: #e8ebf1;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.07);
}

.topLine {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 36px 0 41px 0;
}

.logo__link {
  color: #000000;
  font-family: "LatoBlack", sans-serif;
  font-size: 50px;
  line-height: 0.4;
}

.logo__link span {
  color: #2aacc8;
  font-family: "Lato", sans-serif;
}

.nav__list {
  display: flex;
  list-style-type: none;
}

.nav__item {
  position: relative;
  margin-left: 21px;
  padding: 7px 10px;
  border-top: 5px solid transparent;
  transition: all ease 0.25s;
}

.nav__item:first-child {
  margin-left: 0;
  margin-right: 18px;
}

.nav__item:hover,
.nav__item--active {
  border-top: 5px solid #2aacc8;
}

.nav__item:hover {
  background-color: #ffffff;
}

.nav__item:hover .nav__subList {
  visibility: visible;
  opacity: 1;
}

.nav__link {
  display: flex;
  align-items: center;
  width: 100%;
  color: #000000;
  font-size: 13px;
  line-height: 1.54;
  text-transform: uppercase;
  font-family: "ArialBoldMT", sans-serif;
}

.nav__link:hover,
.nav__link--active {
  color: #2aacc8;
}

.nav__icon {
  margin-left: 6px;
  fill: #000000;
  width: 7px;
  height: 5px;
}

.nav .nav__subList {
  position: absolute;
  top: 100%;
  left: 0;
  width: 205%;
  padding-top: 10px;
  padding-bottom: 5px;
  list-style-type: none;
  background-color: #ffffff;
  z-index: 10;
  transition: 0.35s ease-in-out;
  opacity: 0;
  visibility: hidden;
  border-bottom: 5px solid #2aacc8;
}

.nav .nav__subItem {
  padding-left: 7px;
  padding-right: 7px;
}

.nav .nav__subItem:last-child > a {
  border-bottom: none;
}

.nav .nav__subLink {
  display: block;
  padding-left: 10px;
  color: #000000;
  font-size: 13px;
  line-height: 2.77;
  font-weight: 400;
  font-family: "ArialMT", sans-serif;
  border-bottom: 1px solid #e7e7e7;
}

.nav .nav__subLink:hover {
  color: #2aacc8;
}

/* Кнопка меню-гамбургер
------------------------------------------------*/

.burger {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  z-index: 100;
  padding: 0;
  border: none;
  background: none;
  background: #000000;
  cursor: pointer;
  outline: none;
}

.burger__line {
  width: 24px;
  height: 3px;
  margin-bottom: 5px;
  background: #2aacc8;
}

.burger__line:last-child {
  margin-bottom: 0;
}

.burger--active .burger__line:first-child {
  position: relative;
  top: 8px;
  transform: rotate(45deg);
  transition: all ease 0.5s;
}

.burger--active .burger__line:nth-child(2) {
  opacity: 0;
}

.burger--active .burger__line:last-child {
  position: relative;
  top: -8px;
  transform: rotate(-45deg);
  transition: all ease 0.5s;
}

/*----------------------------------------------*/

/*============================================= */

/*=============================================
        ОСНОВНОЙ КОНТЕНТ
============================================= */

/*---------------------------------------------
  Секция со слайдером
--------------------------------------------- */

.base {
  background-color: #81e4ed;
  padding: 20px 0 8px 0;
}

.baseSlider {
  display: flex;
  width: 100%;
  padding-bottom: 40px;
  box-shadow: inset 0 0 100px rgba(0, 0, 0, 0.12);
  background-color: #e05936;
  background-image: url(../img/images/Layer_4.png);
  background-repeat: no-repeat;
  background-position: right top;
  background-size: auto 100%;
}

.baseSlider__text {
  max-width: 360px;
  margin-top: 102px;
  padding-left: 61px;
  color: #ffffff;
  font-size: 3.57vw;
  font-family: "Lato", sans-serif;
}

.baseSlider__img {
  margin-left: 63px;
}

.owl-carousel .owl-item img {
  width: 15.07vw;
}

/* Пагинация слайдера
------------------------------------------------*/

.owl-carousel .owl-dots {
  width: 100%;
  margin-top: 10px;
  margin-left: -5px;
  text-align: center;
}

.owl-carousel .owl-dots .owl-dot {
  display: inline-block;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  margin-left: 17px;
  background-color: #626262;
  transition: background-color 0.5s ease transform 0.5s ease;
  outline: none;
}

.owl-carousel .owl-dots .owl-dot.active {
  background-color: #ffffff;
}

/*----------------------------------------------*/

/*----------------------------------------------*/

/*---------------------------------------------
  Секция услуги
--------------------------------------------- */

.amenities {
  box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.07);
  border: 1px solid rgba(0, 0, 0, 0.15);
  background: #e8ebf1;
}

.amenities__content {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.amenitiesCard {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(25% - 30px);
  padding-top: 60px;
  padding-bottom: 45px;
}

.amenitiesCard:last-child {
  margin-left: 12px;
}

.amenitiesCard__icon {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #2aacc8;
}

.amenitiesCard__icon .amenitiesCard__svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 21px;
  height: 24px;
  fill: #ffffff;
}

.amenitiesCard__title {
  display: inline-block;
  transform: translateY(-100%);
  margin-left: 20px;
  color: #000000;
  font-size: 1.07vw;
  line-height: 1.33;
  text-transform: uppercase;
  font-family: "ArialBoldMT", sans-serif;
}

.amenitiesCard__desc {
  margin-top: -10px;
  color: #363636;
  font-size: 13px;
  line-height: 1.54;
  font-weight: 400;
  font-family: "ArialMT", sans-serif;
}

.amenitiesCard__readMore {
  display: flex;
  align-items: center;
  margin-top: 20px;
}

.amenitiesCard__readMore .amenitiesCard__readMoreIcon {
  width: 5px;
  height: 7px;
  fill: #000000;
}

.amenitiesCard__readMore a {
  margin-left: 10px;
  color: #2aacc8;
  font-size: 13px;
  line-height: 1.54;
  font-weight: 400;
  font-family: "ArialMT", sans-serif;
}

/*----------------------------------------------*/

/*---------------------------------------------
  Секция последние работы
--------------------------------------------- */

.works {
  padding-top: 15px;
}

.works__icon {
  display: inline-block;
  width: 14px;
  height: 24px;
  fill: #2aacc8;
}

.works__title {
  display: inline-block;
  margin-left: 20px;
  color: #000000;
  font-size: 15px;
  line-height: 1.33;
  text-transform: uppercase;
  font-family: "ArialBoldMT", sans-serif;
  transform: translateY(-20%);
}

.works__content {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: space-between;
  padding: 15px 0 30px 0;
  border-top: 1px solid #dfe1e5;
  border-bottom: 1px solid #dfe1e5;
}

.worksItem {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(24.16667% - 30px);
  position: relative;
  background-color: #000000;
}

.worksItem__img {
  display: block;
  width: 100%;
  height: auto;
}

.worksItem__hover {
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  width: 100%;
  height: 50%;
  position: absolute;
  left: 0;
  top: 50%;
  background-color: #2aacc8;
  transition: opacity ease 0.5s;
}

.worksItem__hover .worksItem__hoverSee {
  display: inline-block;
  width: 35px;
  height: 25px;
  fill: #ffffff;
  cursor: zoom-in;
}

.worksItem__hover .worksItem__hoverLink {
  display: inline-block;
  margin-left: 25px;
  width: 20px;
  height: 26px;
  fill: #ffffff;
}

.worksItem:hover > .worksItem__hover {
  opacity: 1;
}

/* Настройки magnificpopap
------------------------------------------------*/

.mfp-img {
  max-width: none;
  max-height: none !important;
}

/* start state */

.my-mfp-zoom-in .zoom-anim-dialog {
  opacity: 0;
  transition: all 0.5s ease-in-out;
  transform: scale(0.2);
}

/* animate in */

.my-mfp-zoom-in.mfp-ready .zoom-anim-dialog {
  opacity: 1;
  transform: scale(1);
}

/* animate out */

.my-mfp-zoom-in.mfp-removing .zoom-anim-dialog {
  transform: scale(0.2);
  opacity: 0;
}

/* Dark overlay, start state */

.my-mfp-zoom-in.mfp-bg {
  opacity: 0;
  transition: opacity 0.5s ease-out;
}

/* animate in */

.my-mfp-zoom-in.mfp-ready.mfp-bg {
  opacity: 0.8;
}

/* animate out */

.my-mfp-zoom-in.mfp-removing.mfp-bg {
  opacity: 0;
}

/*----------------------------------------------*/

/* Настройки навигации слик-слайдера
------------------------------------------------*/

.slick-leftArrow,
.slick-rightArrow {
  position: absolute;
  display: inline-block;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #81e4ed;
}

.slick-rightArrow {
  top: -40px;
  right: 30px;
  background-image: url(../img/images/Shape_1_copy.svg);
  background-repeat: no-repeat;
  background-position: center center;
}

.slick-leftArrow {
  top: -40px;
  right: 30px;
  margin-right: 30px;
  background-image: url(../img/images/Shape_1.svg);
  background-repeat: no-repeat;
  background-position: center center;
}

