.main__content {
  display: flex;
  flex-wrap: wrap;
  padding-top: 20px;
  padding-bottom: 40px;
}

/*---------------------------------------------
  Секция рекомендации
--------------------------------------------- */

.testimonials {
  width: 50%;
}

.testimonials__icon {
  display: inline-block;
  width: 24px;
  height: 23px;
  fill: #2aacc8;
}

.testimonials__text {
  max-width: 425px;
  margin-bottom: 30px;
  padding-right: 10px;
  color: #363636;
  font-size: 14px;
  line-height: 1.71;
  font-weight: 400;
  font-family: "ArialMT", sans-serif;
}

.testimonials span {
  padding-right: 10px;
  font-family: "ArialBoldItalicMT", sans-serif;
  font-weight: 700;
  font-style: italic;
}

