/*---------------------------------------------
  Секция клиенты
--------------------------------------------- */

.clients {
  width: 50%;
}

.clients__icon {
  display: inline-block;
  width: 24px;
  height: 20px;
  fill: #2aacc8;
}

.clients__content {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.clients__content .clients__item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 33.3%;
