/*----------------------------------------------*/

.download {
  display: flex;
  align-items: center;
  padding: 37px 40px 35px 31px;
  border: 1px solid #81e4ed;
  background-color: #e8fcfe;
}

.download a {
  display: inline-block;
}

.download__text {
  display: inline-block;
