@media screen and (max-width: 996px) {
  .wrapper {
    padding-left: 50px;
    padding-right: 50px;
  }

  .topLine {
    display: block;
    text-align: center;
  }

  .nav {
    display: flex;
    justify-content: center;
    margin-top: 30px;
  }

  .nav .nav__subItem {
    text-align: left;
  }

  .baseSlider__img {
    margin-left: 0;
  }

  .amenities__content {
